<template>
  <div class="train">
		<div class="title clearfix">
			<h3 class="fl"></h3>
			<div class="fr" style="margin-top: 6px;">
				<goback :isGoBack="gobackFalg">
					<div class="fr" style=""><i class="fa fa-arrow-left"></i></div>
				</goback>
			</div>
		</div>
		<div class="train-main clearfix">
			<div class="train-main-top clearfix">
				<div>
					<el-button type="success"  @click="toStart" :disabled="start || !isOnline"><i class="fa fa-play"></i></el-button>
					<el-button type="success"  @click="toPause('finish')"  :disabled="topause" v-show="isPause"><i class="fa fa-pause"></i></el-button>
					<el-button type="success" @click="toPause('jixun')"  :disabled="!start" v-show="!isPause"><i class="fa fa-play"></i></el-button>
				</div>
				<!-- voiceNum -->
				<div class="fl voice-num">
					<p class="fl"></p>
					<el-input-number class="num" v-model="value" :step="1" @change="change" :min="1" :max="5"></el-input-number>
				</div>
			</div>
			<div class="train-main-middle">
				<el-steps :space="100" direction="vertical" :active="step">
				  <el-step title=" 1" description=""></el-step>
				  <el-step title=" 2" description=""></el-step>
				</el-steps>
			</div>
		</div>
		<div class="button">
			<div class="fl">
				<el-button type="info" :disabled="'finish' != isfinish" @click="angin">
					<i class="fa fa-repeat"></i>
					
				</el-button>
			</div>
			<div class="fr">
				<el-button type="success" class="btnd" @click="goNext" :disabled="'finish' != isfinish">&nbsp;&nbsp;&nbsp;&nbsp;<i class="fa fa-chevron-right"></i></el-button>
			</div>
		</div>
		<!-- loading -->
		<div>
			<StartTips :loadOver="loadOver"></StartTips>
		</div>
		<div class="confirm" v-show="isShowConfirm">
			<h3></h3>
			<p>{{feedbackTime}}&nbsp;{{value}}&nbsp;</p>
			<button class="cancal" @click="close_confirm"></button>
			<button @click="save_confirm"></button>
		</div>
  </div>
</template>
<script>
import Goback from '../commonvue/backup'
import StartTips from '../commonvue/startprepare'
export default {
	components:{
		Goback,
		StartTips
	},
 	data(){
 		return {
 			loadOver: false,
 			hz:'',
 			current:'',
 			currentgame:'',
 			currentsign:'',
 			// 
 			start:false,
 			// 
 			isOnline : false,
 			// 
 			isfinish:'test',
 			isPause:true,
 			topause:true,
 			wsData :'',
 			step:0,
 			// 
 			successTimesNum:0,
 			// 
 			successAllNum:0,
 			// 
 			successResponseTime:0,
 			value: 5,
 			feedbackTime:5,
 			isShowConfirm:false
 		}
 	},
 	// props:{
 	// 	socket:null,
 	// },
 	mounted(){
 		// 
 		this.loadOver = true;
 		// 
 		this.db= this.$route.query.crtdb;
 		this.hz = this.$route.query.data;
 		this.current = this.$route.query.isEar;
 		this.currentgame = this.$route.query.crtgame;
 		this.currentsign = this.$route.query.crtsign
 		// websocket
 		var that = this;
 		Vm.$on('wsmsg',(msg)=>{
 			that.wsData = msg;
 		})
 		Vm.$on('servermsg',(msg,res,str)=>{
 			// console.log(msg.mescon,res,str)
 			if(!that.closeLay){
	 			Utils.ctld_tran_isOnline(that,res,str);
	 		}
 		});
 	},
 	watch:{
		wsData:function(){
			// 
			if(this.isOnline){
				this.loadOver = false;
			};
			console.log(this.wsData.params)
			// 
			this.stops();
			// 
			this.successNum();
		}
 	},
 	computed:{
 		gobackFalg:function(){
 			let backfalg = this.start && ('finish' != this.isfinish)
 			return backfalg;
 		},
 		averageTime :function(){
 			let averageTime = Math.round(this.successResponseTime/this.successAllNum);
 			if(averageTime > 5 || isNaN(averageTime)){return averageTime = 5};
 			if(averageTime < 1){return averageTime = 1};
 			return  averageTime;
 		}
 	},
 	beforeRouteLeave(to,form,next){
 		// console.log(to,form,next);
 		this.closeLay = true;
 		next();
 	},
 	methods:{
 		// 
 		change(evt){
 			// 
 			const params = {
	    		'tonetype':this.xcurrent,//
		    	'testType':'zhuting',//
		    	'game':this.currentgame,//
		    	'time':evt,//
		    };
		    Utils.chang_params(this,params);
 		},
 		// 
 		toStart(){
			// 
			var params = {
				'ear':'A',//
	    		'tonetype':this.currentsign,//
		    	'testType':'zhuting',//
		    	'game':this.currentgame,//
		    	'time':this.value,//
		    	'hz': '500',//
		    	'level':this.$route.query['level'],
		    	'validTime':'3',//
		    	'db':'50'//this.currentDb
			};
			// console.log(params)
	    	var argument = this.wskt.wstoctld('games_train_start',params);
	    	websocket.send(JSON.stringify(argument));
 			this.start = !this.start;
 			this.step = 1;
 			this.topause = false;
 		},
 		successNum(){
 			if(this.wsData.params['success'] === "false") return false;
			const callback = ()=>{
				this.successAllNum++;//
				// 
				this.feedbackTime = +this.wsData.params['feedbackTime'].toFixed(2);
				this.successResponseTime += this.feedbackTime;
				this.isShowConfirm = true;
			}
			Utils.successNum(this,30,callback);
 		},
 		close_confirm(){
 			this.isShowConfirm = false;
 		},
 		save_confirm(){
 			this.isShowConfirm = false;
 			const data = {
 				"user_id":sessionStorage.getItem('user_id'),
 				'feedbackTime':this.feedbackTime,
 				'playTimer':this.value,
 				'games':this.currentgame
 			};
 			this.$ajax.post('/game/log',data).then((res)=>{
 				if(res.code !== 200){
 					msgTipsErr(this,"")
 				}
 			});
 		},
 		// 
 		angin(){
 			Utils.angin(this);
 		},
 		// 
 		toPause(str){
 			Utils.toPause(str,this);
 		},
 		stops(){
 			Utils.stops(this);
 		},
 		// 
 		_trainOverTips(){
 			Utils._trainOverTips(this);
 		},
 		goNext(){
 			window.isToggle = false;
 			var param = this.hz;
 			var ear = this.current;
 			var games = this.currentgame;
 			var sign = this.currentsign;
 			websocket.send(JSON.stringify(this.wskt.goaudioplan('zhuting','official',games)));
 			// Utils._setVolumNum(this,{'decibel':this.averageTime,'test_id':sessionStorage.getItem('test_id')});
	    	this.$router.push({ path: '/home/zhuting/audiometry',query:{data:param,crtdb:this.db,isEar:ear,crtgame:games,crtsign:sign,'level':this.$route.query['level']}})
 		}
 	}
}
</script>
<style lang ="less" scoped>
.train{position: relative;}
.confirm{
	background: #eef1f6;
	position: fixed;
	left:50%;
	top:40%;
	transform:translate(-50%,-50%);
	border-radius:10px;
	width: 400px;
	height: 250px;
	box-sizing: border-box;
	padding: 10px;
	z-index:3000;
	box-shadow: 0px 0px 50px #000;
	h3{
		font-size: 20px;
		line-height: 30px;
		text-align: center;
	}
	p{
		margin-top: 20px;
		text-indent:2em;
		line-height: 100px;
		height: 130px;
		font-size: 18px;
	}
	button{
		width: 100px;
		height: 40px;
		background: #5bb75b;
		border:1px solid #5bb75b;
		box-shadow: 2px 2px 2px #000;
		float: right;
		margin-right:20px;
	}
	.cancal{
		float: left;
		margin-left:20px;
		background-color: #dd4c4c;
		border:1px solid #dd4c4c;
	}
}
</style>



